"use client";

import React from 'react';
import { useBackground } from './BackgroundManager';
import { Settings2, Zap, Wind, Palette } from 'lucide-react';

export default function SettingsPanel() {
    const {
        theme, setTheme,
        colorTheme, setColorTheme,
        speed, setSpeed,
        intensity, setIntensity
    } = useBackground();

    const colorThemes: { id: any, name: string, colors: string[] }[] = [
        { id: 'ocean', name: 'Ocean', colors: ['#3b82f6', '#06b6d4'] },
        { id: 'forest', name: 'Forest', colors: ['#10b981', '#4ade80'] },
        { id: 'sunset', name: 'Sunset', colors: ['#f43f5e', '#fb923c'] },
        { id: 'cyber', name: 'Cyber', colors: ['#8b5cf6', '#d946ef'] },
        { id: 'plasma', name: 'Plasma', colors: ['#f59e0b', '#ef4444'] },
    ];

    return (
        <div className="fixed top-24 right-6 z-50 w-72 group">
            <div className="relative p-6 bg-black/40 backdrop-blur-2xl rounded-3xl border border-white/10 shadow-2xl transition-all duration-500 hover:border-white/20">
                <div className="flex items-center gap-3 mb-8">
                    <div className="p-2 bg-white/5 rounded-xl border border-white/10">
                        <Settings2 className="w-4 h-4 text-white/70" />
                    </div>
                    <h3 className="text-sm font-bold text-white tracking-widest uppercase">System Config</h3>
                </div>

                <div className="space-y-8">
                    {/* Simulation Speed */}
                    <div className="space-y-4">
                        <div className="flex justify-between items-center">
                            <label className="text-[10px] font-black text-white/30 uppercase tracking-tighter flex items-center gap-2">
                                <Wind className="w-3 h-3" /> Temporal Flow
                            </label>
                            <span className="text-[10px] font-mono text-white/60">{speed.toFixed(1)}x</span>
                        </div>
                        <input
                            type="range" min="0.1" max="3" step="0.1"
                            value={speed} onChange={(e) => setSpeed(parseFloat(e.target.value))}
                            className="w-full h-1 bg-white/10 rounded-lg appearance-none cursor-pointer accent-white hover:accent-blue-400 transition-colors"
                        />
                    </div>

                    {/* Glow Intensity */}
                    <div className="space-y-4">
                        <div className="flex justify-between items-center">
                            <label className="text-[10px] font-black text-white/30 uppercase tracking-tighter flex items-center gap-2">
                                <Zap className="w-3 h-3" /> Photon Density
                            </label>
                            <span className="text-[10px] font-mono text-white/60">{intensity.toFixed(1)}x</span>
                        </div>
                        <input
                            type="range" min="0.5" max="5" step="0.1"
                            value={intensity} onChange={(e) => setIntensity(parseFloat(e.target.value))}
                            className="w-full h-1 bg-white/10 rounded-lg appearance-none cursor-pointer accent-white hover:accent-pink-400 transition-colors"
                        />
                    </div>

                    {/* Color Themes */}
                    <div className="space-y-4">
                        <label className="text-[10px] font-black text-white/30 uppercase tracking-tighter flex items-center gap-2">
                            <Palette className="w-3 h-3" /> Chroma Shift
                        </label>
                        <div className="grid grid-cols-5 gap-2">
                            {colorThemes.map((ct) => (
                                <button
                                    key={ct.id}
                                    onClick={() => setColorTheme(ct.id)}
                                    className={`group/btn relative h-8 rounded-lg border transition-all duration-300 ${colorTheme === ct.id ? 'border-white/40 scale-110 shadow-lg' : 'border-transparent hover:border-white/10'
                                        }`}
                                    title={ct.name}
                                >
                                    <div className="absolute inset-1 rounded-md overflow-hidden flex flex-col">
                                        <div className="flex-1" style={{ backgroundColor: ct.colors[0] }} />
                                        <div className="flex-1" style={{ backgroundColor: ct.colors[1] }} />
                                    </div>
                                    {colorTheme === ct.id && (
                                        <div className="absolute -bottom-1 left-1/2 -translate-x-1/2 w-1 h-1 bg-white rounded-full" />
                                    )}
                                </button>
                            ))}
                        </div>
                    </div>
                </div>

                <div className="mt-10 pt-6 border-t border-white/5">
                    <p className="text-[8px] text-center text-white/20 uppercase font-bold tracking-[0.2em]">Neural Engine v1.0.4</p>
                </div>
            </div>
        </div>
    );
}
