"use client";

import React, { useRef, useMemo } from 'react';
import { Canvas, useFrame } from '@react-three/fiber';
import { Float } from '@react-three/drei';
import * as THREE from 'three';

const THEME_COLORS = {
    ocean: { top: "#3b82f6", bottom: "#ffffff" },
    forest: { top: "#10b981", bottom: "#ffffff" },
    sunset: { top: "#f43f5e", bottom: "#ffffff" },
    cyber: { top: "#8b5cf6", bottom: "#ffffff" },
    plasma: { top: "#f59e0b", bottom: "#ffffff" }
};

function Capsule({ colorTop, colorBottom, speed, intensity }: any) {
    const mesh = useRef<THREE.Group>(null);
    const pos = useMemo(() => [
        (Math.random() - 0.5) * 30,
        (Math.random() - 0.5) * 30,
        (Math.random() - 0.5) * 10
    ] as [number, number, number], []);

    const rotationSpeed = (0.5 + Math.random()) * speed;

    useFrame((state) => {
        if (!mesh.current) return;
        mesh.current.rotation.x += rotationSpeed * 0.01;
        mesh.current.rotation.y += rotationSpeed * 0.02;

        // Mouse interaction tilt
        const mouseX = state.mouse.x * 0.5;
        const mouseY = state.mouse.y * 0.5;
        mesh.current.rotation.x += mouseY * 0.05;
        mesh.current.rotation.y += mouseX * 0.05;
    });

    return (
        <group ref={mesh} position={pos}>
            <Float speed={2 * speed} rotationIntensity={1} floatIntensity={2}>
                <mesh position={[0, 0.4, 0]}>
                    <capsuleGeometry args={[0.3, 0.6, 4, 16]} />
                    <meshStandardMaterial
                        color={colorTop}
                        emissive={colorTop}
                        emissiveIntensity={intensity}
                    />
                </mesh>
                <mesh position={[0, -0.4, 0]}>
                    <capsuleGeometry args={[0.3, 0.6, 4, 16]} />
                    <meshStandardMaterial
                        color={colorBottom}
                        emissive={colorBottom}
                        emissiveIntensity={intensity * 0.5}
                    />
                </mesh>
            </Float>
        </group>
    );
}

interface MedicineCapsulesBackgroundProps {
    colorTheme?: 'ocean' | 'forest' | 'sunset' | 'cyber' | 'plasma';
    speed?: number;
    intensity?: number;
}

export default function MedicineCapsulesBackground({ colorTheme = 'cyber', speed = 1, intensity = 1 }: MedicineCapsulesBackgroundProps) {
    const colors = THEME_COLORS[colorTheme as keyof typeof THEME_COLORS] || THEME_COLORS.cyber;

    return (
        <Canvas camera={{ position: [0, 0, 15], fov: 45 }}>
            <ambientLight intensity={0.5} />
            <pointLight position={[10, 10, 10]} intensity={1.5 * intensity} />
            {Array.from({ length: 25 }).map((_, i) => (
                <Capsule
                    key={i}
                    colorTop={colors.top}
                    colorBottom={colors.bottom}
                    speed={speed}
                    intensity={intensity}
                />
            ))}
        </Canvas>
    );
}
