"use client";

import React, { useRef, useMemo } from 'react';
import { Canvas, useFrame } from '@react-three/fiber';
import { Points, PointMaterial, Line } from '@react-three/drei';
import * as THREE from 'three';

const THEME_COLORS = {
    ocean: "#3b82f6",
    forest: "#10b981",
    sunset: "#f43f5e",
    cyber: "#8b5cf6",
    plasma: "#f59e0b"
};

function Network({ colorTheme = 'cyber', speed = 1, intensity = 1 }: { colorTheme: string, speed: number, intensity: number }) {
    const pointsRef = useRef<THREE.Points>(null);
    const color = THEME_COLORS[colorTheme as keyof typeof THEME_COLORS] || THEME_COLORS.cyber;

    const count = 150;
    const [positions, connections] = useMemo(() => {
        const pos = new Float32Array(count * 3);
        const con = [];
        for (let i = 0; i < count; i++) {
            pos[i * 3] = (Math.random() - 0.5) * 20;
            pos[i * 3 + 1] = (Math.random() - 0.5) * 20;
            pos[i * 3 + 2] = (Math.random() - 0.5) * 20;
        }

        for (let i = 0; i < count; i++) {
            for (let j = i + 1; j < count; j++) {
                const dx = pos[i * 3] - pos[j * 3];
                const dy = pos[i * 3 + 1] - pos[j * 3 + 1];
                const dz = pos[i * 3 + 2] - pos[j * 3 + 2];
                const dist = Math.sqrt(dx * dx + dy * dy + dz * dz);
                if (dist < 4) con.push([pos[i * 3], pos[i * 3 + 1], pos[i * 3 + 2], pos[j * 3], pos[j * 3 + 1], pos[j * 3 + 2]]);
            }
        }
        return [pos, con.slice(0, 300)];
    }, []);

    useFrame((state) => {
        if (!pointsRef.current) return;
        const time = state.clock.getElapsedTime() * speed;
        pointsRef.current.rotation.y = time * 0.05;
        pointsRef.current.rotation.x = time * 0.03;
    });

    return (
        <group>
            <Points ref={pointsRef} positions={positions} stride={3}>
                <PointMaterial
                    transparent
                    color={color}
                    size={0.15 * intensity}
                    sizeAttenuation={true}
                    depthWrite={false}
                    blending={THREE.AdditiveBlending}
                />
            </Points>
            {connections.map((c, i) => (
                <Line
                    key={i}
                    points={[[c[0], c[1], c[2]], [c[3], c[4], c[5]]]}
                    color={color}
                    lineWidth={0.5}
                    transparent
                    opacity={(0.05 + Math.sin(Date.now() * 0.001 + i) * 0.02) * intensity}
                    blending={THREE.AdditiveBlending}
                />
            ))}
        </group>
    );
}

interface NeuralNetworkBackgroundProps {
    colorTheme?: 'ocean' | 'forest' | 'sunset' | 'cyber' | 'plasma';
    speed?: number;
    intensity?: number;
}

export default function NeuralNetworkBackground({ colorTheme = 'cyber', speed = 1, intensity = 1 }: NeuralNetworkBackgroundProps) {
    return (
        <Canvas camera={{ position: [0, 0, 15], fov: 45 }}>
            <ambientLight intensity={0.5} />
            <Network colorTheme={colorTheme} speed={speed} intensity={intensity} />
        </Canvas>
    );
}
